var field;
if (fieldId.startsWith("1_")) {
type = 1;
field = fieldId.substring(2);
}
else if (fieldId.startsWith("2_")) {
type = 2;
field = fieldId.substring(2);
}
else {
type = 0;
field = fieldId;
}
var userIds = mainFormData[field];
if (userIds != undefined && userIds != "" && userIds != null) {
